import type {
  Perfume,
  Category,
  Brand,
  PaginatedResult,
  PaginationParams,
  PerfumeFilters,
  SortOptions,
  ApiResponse,
  AuthResponse,
  Admin,
} from '@perfume-catalog/shared';

const API_BASE = '/api';

// Generic fetch helper
async function fetchApi<T>(
  endpoint: string,
  options?: RequestInit
): Promise<ApiResponse<T>> {
  const response = await fetch(`${API_BASE}${endpoint}`, {
    ...options,
    headers: {
      'Content-Type': 'application/json',
      ...options?.headers,
    },
  });
  return response.json();
}

// ============================================
// Perfume API Client
// ============================================
export const perfumeApi = {
  async getAll(
    params: PaginationParams,
    filters?: PerfumeFilters,
    sort?: SortOptions
  ): Promise<PaginatedResult<Perfume>> {
    const searchParams = new URLSearchParams({
      page: params.page.toString(),
      pageSize: params.pageSize.toString(),
    });

    if (filters?.categoryId) searchParams.set('categoryId', filters.categoryId);
    if (filters?.brandId) searchParams.set('brandId', filters.brandId);
    if (filters?.isFeatured !== undefined) searchParams.set('isFeatured', filters.isFeatured.toString());
    if (filters?.hasDiscount) searchParams.set('hasDiscount', 'true');
    if (filters?.search) searchParams.set('search', filters.search);
    if (sort?.field) searchParams.set('sortField', sort.field);
    if (sort?.direction) searchParams.set('sortDirection', sort.direction);

    const res = await fetchApi<PaginatedResult<Perfume>>(`/perfumes?${searchParams}`);
    if (!res.success || !res.data) throw new Error(res.error || 'فشل تحميل العطور');
    return res.data;
  },

  async getById(id: string): Promise<Perfume | null> {
    const res = await fetchApi<Perfume>(`/perfumes/${id}`);
    return res.success ? res.data! : null;
  },

  async getMostSold(limit: number = 8): Promise<Perfume[]> {
    const res = await fetchApi<Perfume[]>(`/perfumes?endpoint=most-sold&limit=${limit}`);
    return res.success ? res.data! : [];
  },

  async getFeatured(limit: number = 8): Promise<Perfume[]> {
    const res = await fetchApi<Perfume[]>(`/perfumes?endpoint=featured&limit=${limit}`);
    return res.success ? res.data! : [];
  },

  async getDiscounted(limit?: number): Promise<Perfume[]> {
    const url = limit ? `/perfumes?endpoint=discounted&limit=${limit}` : '/perfumes?endpoint=discounted';
    const res = await fetchApi<Perfume[]>(url);
    return res.success ? res.data! : [];
  },

  async getByCategory(categoryId: string, params: PaginationParams): Promise<PaginatedResult<Perfume>> {
    return this.getAll(params, { categoryId });
  },

  async getByBrand(brandId: string, params: PaginationParams): Promise<PaginatedResult<Perfume>> {
    return this.getAll(params, { brandId });
  },

  async getRelated(perfumeId: string, limit: number = 4): Promise<Perfume[]> {
    const res = await fetchApi<Perfume[]>(`/perfumes/${perfumeId}?related=true&limit=${limit}`);
    return res.success ? res.data! : [];
  },

  async create(data: Partial<Perfume>): Promise<Perfume> {
    const res = await fetchApi<Perfume>('/perfumes', {
      method: 'POST',
      body: JSON.stringify(data),
    });
    if (!res.success || !res.data) throw new Error(res.error || 'فشل إضافة العطر');
    return res.data;
  },

  async update(id: string, data: Partial<Perfume>): Promise<Perfume> {
    const res = await fetchApi<Perfume>(`/perfumes/${id}`, {
      method: 'PUT',
      body: JSON.stringify(data),
    });
    if (!res.success || !res.data) throw new Error(res.error || 'فشل تحديث العطر');
    return res.data;
  },

  async delete(id: string): Promise<boolean> {
    const res = await fetchApi<{ id: string }>(`/perfumes/${id}`, { method: 'DELETE' });
    return res.success;
  },

  async toggleFeatured(id: string): Promise<Perfume> {
    const res = await fetchApi<Perfume>(`/perfumes/${id}`, {
      method: 'PATCH',
      body: JSON.stringify({ action: 'toggleFeatured' }),
    });
    if (!res.success || !res.data) throw new Error(res.error || 'فشل تحديث العطر');
    return res.data;
  },

  async updateDiscount(id: string, discount: number): Promise<Perfume> {
    const res = await fetchApi<Perfume>(`/perfumes/${id}`, {
      method: 'PATCH',
      body: JSON.stringify({ action: 'updateDiscount', discount }),
    });
    if (!res.success || !res.data) throw new Error(res.error || 'فشل تحديث الخصم');
    return res.data;
  },
};

