// ============================================
// Auth API Client
// ============================================
export const authApi = {
  async signIn(email: string, password: string): Promise<AuthResponse> {
    const res = await fetchApi<AuthResponse>('/auth', {
      method: 'POST',
      body: JSON.stringify({ email, password }),
    });
    if (!res.success || !res.data) throw new Error(res.error || 'فشل تسجيل الدخول');
    return res.data;
  },

  async getCurrentUser(): Promise<Admin | null> {
    const res = await fetchApi<Admin>('/auth');
    return res.success ? res.data! : null;
  },

  async signOut(): Promise<void> {
    await fetchApi('/auth', { method: 'DELETE' });
  },
};

// ============================================
// Discount API Client
// ============================================
export const discountApi = {
  async getAll(limit?: number): Promise<Perfume[]> {
    const url = limit ? `/discounts?limit=${limit}` : '/discounts';
    const res = await fetchApi<Perfume[]>(url);
    return res.success ? res.data! : [];
  },

  async apply(perfumeId: string, discount: number): Promise<Perfume> {
    const res = await fetchApi<Perfume>('/discounts', {
      method: 'POST',
      body: JSON.stringify({ perfumeId, discount }),
    });
    if (!res.success || !res.data) throw new Error(res.error || 'فشل تطبيق الخصم');
    return res.data;
  },

  async remove(perfumeId: string): Promise<Perfume> {
    const res = await fetchApi<Perfume>('/discounts', {
      method: 'DELETE',
      body: JSON.stringify({ perfumeId }),
    });
    if (!res.success || !res.data) throw new Error(res.error || 'فشل إزالة الخصم');
    return res.data;
  },
};

